.generate-widths(@n, @i: 0) when (@i =< @n) {
  .w-@{i} {
    width: percentage(@i / 100);
    float: @left;
  }
  .generate-widths(@n, (@i + 5));
}
.generate-widths(100);

.gradientHover(@posweb,@pos,@colorfrom,@colorto,@colorfromper,@colortoper){
  background: @colorfrom;
  background: -moz-linear-gradient(@posweb, @colorfrom @colorfromper, @colorto @colortoper);
  background: -webkit-linear-gradient(@posweb, @colorfrom @colorfromper,@colorto @colortoper);
  background: linear-gradient(@pos, @colorfrom @colorfromper,@colorto @colortoper);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='@{colorfrom}', endColorstr='@{colorto}',GradientType=0 );

  &:hover{
    background: @colorto;
    background: -moz-linear-gradient(@posweb, @colorto @colorfromper, @colorfrom @colortoper);
    background: -webkit-linear-gradient(@posweb, @colorto @colorfromper,@colorfrom @colortoper);
    background: linear-gradient(@pos, @colorto @colorfromper,@colorfrom @colortoper);
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='@{colorto}', endColorstr='@{colorfrom}',GradientType=0 );
  }
}

.newfont(@name){
  @font-face {
    font-family: @name;
    // src for each browser/system
    src: url('/fonts/@{name}.eot');
    src: url('/fonts/@{name}.eot?#iefix') format('embedded-opentype'),
    url('/fonts/@{name}.woff2') format('woff2'),
    url('/fonts/@{name}.woff') format('woff'),
    url('/fonts/@{name}.ttf')  format('truetype'),
    url('/fonts/@{name}.svg#svgFontName') format('svg');
  }
}

.fsize(@w,@h){
  -webkit-transform:scale(@w, @h);
  -moz-transform:scale(@w, @h);
  -ms-transform:scale(@w, @h);
  -o-transform:scale(@w, @h);
  transform:scale(@w,@h);
}
